.page {
  margin: 0px;
  max-width: 100%;
  overflow: hidden; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

*::-webkit-scrollbar {
  width: 0.4em; }

*::-webkit-scrollbar {
  width: 0.4em; }

*::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0); }

*::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.1);
  outline: 1px solid slategrey; }

.container {
  display: block; }

.header {
  background-color: #27ade3;
  text-align: left; }
  .header h2,
  .header h5 {
    color: white;
    font-weight: bold;
    margin-bottom: 0px;
    padding: 10px 20px;
    text-transform: uppercase; }

.header-actions {
  font-weight: bold;
  font-size: 18px; }
  .header-actions .data {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 30px 10% 20px 10%; }
    .header-actions .data svg {
      vertical-align: bottom;
      color: #27ade3; }

.title {
  background-color: #27ade3;
  color: white;
  font-size: 26px;
  padding: 7px;
  padding-left: 20px;
  text-align: left; }

.subTitle {
  display: -webkit-flex;
  display: flex;
  color: gray;
  font-size: 18px;
  margin-top: -10px; }

.place {
  width: 50%;
  text-align: left; }

.charge {
  display: -webkit-flex;
  display: flex;
  font-size: 20px;
  font-weight: bold;
  margin: 10px 0px; }

.time {
  width: 50%;
  text-align: right; }

.link {
  text-decoration: none;
  color: black; }

.description {
  margin-top: 0px;
  margin-bottom: 5px;
  text-align: justify; }

